            </el-col>
          </el-row>
          <el-row type="flex" justify="center">
            <el-col>
              <el-form-item>
                <el-button type="primary" @click="predict" :disabled="isRunning">
                  BMS推定
                </el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <div class="predicted-bms">
          <span class="predicted-bms__label">BMS =</span>
          <div v-loading="isRunning" style="display: inline-block">
            <span class="predicted-bms__value">{{ predictedBms ? predictedBms : '?' }}</span>
          </div>
        </div>
      </el-col>
    </el-row>
    <el-divider></el-divider>
    <el-row>
      <el-col>
        <el-form label-width="200px" @keyup.enter="search">
          <el-row type="flex" justify="center">
            <el-col :xl="6" :lg="8" :md="10" :sm="12" :xs="14">
              <el-form-item label="BMS">
                <el-input v-model="searchBms" type="number" min="1" max="12"></el-input>
              </el-form-item>
              <el-form-item label="僧帽筋厚さ">
                <el-input v-model="searchTrapeziusThickness" type="number" min="0">
                  <template #append>cm</template>
                </el-input>
