<template>
  <div class="echo-image-upload">
    <el-upload
      ref="upload"
      :class="{
        'el-upload--hidden': uploadFile,
        'el-upload-list--hidden': !uploadFile,
      }"
      :name="`${name}.jpg`"
      drag
      action=""
      :auto-upload="false"
      :multiple="false"
      list-type="picture"
      accept="image/jpeg"
      :before-upload="beforeUpload"
      :before-remove="beforeRemove"
      :on-remove="onRemove"
      :on-change="onChange"
      :on-preview="onPreview"
      :on-success="onSuccess"
      :on-error="onError"
      :on-progress="onProgress"
      :limit="1"
      :on-exceed="onExceed"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text"><slot></slot></div>
    </el-upload>
  </div>
</template>

<script lang="ts">
import { computed, defineComponent, ref } from 'vue'
import { ElUpload, UploadFile } from 'element-plus/lib/el-upload/src/upload.type'

export default defineComponent({
  name: 'EchoImageUpload',
  props: {
    name: {
      type: String,
      required: true,
    },
  },
  setup () {
    const upload = ref<ElUpload>()
    const uploadFile = computed(() => upload.value?.uploadFiles?.[0]?.raw)

    const beforeUpload = (file: UploadFile, fileList: UploadFile[]) => {
      console.log('before-upload', file, fileList)
    }

    const beforeRemove = (file: UploadFile, fileList: UploadFile[]): boolean => {
      console.log('before-remove', file, fileList)
      return true
    }

    const onRemove = (file: UploadFile, fileList: UploadFile[]) => {
      console.log('on-remove', file, fileList)
    }

    const onChange = (file: UploadFile, fileList: UploadFile[]) => {
      console.log('on-change', file, fileList)
    }

    const onPreview = (file: UploadFile) => {
      console.log('on-preview', file)
    }

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const onSuccess = (response: any, file: UploadFile, fileList: UploadFile[]) => {
      console.log('on-success', response, file, fileList)
    }

    const onProgress = (event: ProgressEvent, file: UploadFile, fileList: UploadFile[]) => {
      console.log('on-progress', event, file, fileList)
    }

    const onError = (error: Error, file: UploadFile, fileList: UploadFile[]) => {
      console.log('on-error', error, file, fileList)
    }

    const onExceed = (fileList: FileList) => {
      console.log('on-exceed', fileList)
    }

    return {
      // refs
      upload,
      // computed
      uploadFile,
      // methods
      beforeUpload,
      beforeRemove,
      onRemove,
      onChange,
      onPreview,
      onSuccess,
      onProgress,
      onError,
      onExceed,
    }
  },
})
</script>

