import { createApp } from 'vue'
import {
  ElButton,
  ElCol,
  ElCarousel,
  ElCarouselItem,
  ElDivider,
  ElForm,
  ElFormItem,
  ElIcon,
  ElImage,
  ElInput,
  ElLoading,
  ElRow,
  ElTable,
  ElTableColumn,
  ElUpload,
} from 'element-plus'
import './element-variables.scss'
import App from './App.vue'
import './registerServiceWorker'
import router from './router'
import store from './store'

createApp(App)
