
import axios from 'axios'
import { defineComponent, reactive, ref } from 'vue'
import EchoImageUpload from '@/components/EchoImageUpload.vue'
import exampleImages from '../assets/example-images.json'
// import exampleImages from '../assets/example-images.10.json' // For development

interface ExampleImageInfo {
  id?: number,
  image?: string,
  url?: string,
  bms?: number,
  trapeziusThickness?: number,
  trapeziusSubcutaneousFatThickness?: number,
  subcutaneousFatThickness?: number,
  latissimusThickness?: number,
}

export default defineComponent({
  name: 'Prediction',
  components: { EchoImageUpload },
  setup () {
    const l2gh = ref<typeof EchoImageUpload>()
    const l2gl = ref<typeof EchoImageUpload>()
    const l3gh = ref<typeof EchoImageUpload>()
    const l3gl = ref<typeof EchoImageUpload>()
    const isRunning = ref(false)
    const predictedBms = ref()
    const searchBms = ref()
    const searchTrapeziusThickness = ref()
    const searchTrapeziusSubcutaneousFatThickness = ref()
    const searchSubcutaneousFatThickness = ref()
    const searchLatissimusThickness = ref()
    const similarImages = reactive<ExampleImageInfo[]>([{}])
    const carouselReloadKey = ref(0)

    const predict = async () => {
      try {
        console.log('Start prediction.')
        isRunning.value = true

        const data = new FormData()
        l2gh.value?.uploadFile && data.append(l2gh.value.name, l2gh.value.uploadFile)
        l2gl.value?.uploadFile && data.append(l2gl.value.name, l2gl.value.uploadFile)
        l3gh.value?.uploadFile && data.append(l3gh.value.name, l3gh.value.uploadFile)
        l3gl.value?.uploadFile && data.append(l3gl.value.name, l3gl.value.uploadFile)

        await axios.post('https://aimeat-cloudrun-prediction-szh77npjnq-an.a.run.app', data, {
          headers: { 'content-type': 'multipart/form-data' },
        }).then(response => {
          console.log(response)
          predictedBms.value = response.data.BMS
          searchBms.value = predictedBms.value
        })
      } finally {
        isRunning.value = false
      }
    }

    const search = () => {
      console.log('Start similar images search.')

      if (
        !searchBms.value ||
        !searchTrapeziusThickness.value ||
        !searchTrapeziusSubcutaneousFatThickness.value ||
        !searchSubcutaneousFatThickness.value ||
        !searchLatissimusThickness.value
      ) {
        return
      }

      const distances = exampleImages.map(i => {
        // TODO: Refine calcuration algorithm of distance
        const distance =
          (searchTrapeziusThickness.value - i.trapeziusThickness) ** 2 +
          (
            searchTrapeziusSubcutaneousFatThickness.value - i.trapeziusSubcutaneousFatThickness
          ) ** 2 +
          (searchSubcutaneousFatThickness.value - i.subcutaneousFatThickness) ** 2 +
          (searchLatissimusThickness.value - i.latissimusThickness) ** 2
        return { id: i.id, distance }
      }).sort((a, b) => {
        return a.distance - b.distance
      })

      const similarIds = distances.slice(0, 5).map(d => d.id)

      similarImages.splice(0, similarImages.length) // clear similarImages
      similarIds
        .map(i => exampleImages.find(img => img.id === i))
        .forEach(i => {
          similarImages.push({
            ...i,
            url: `https://storage.googleapis.com/carcassdb-v2-stg/aimeat_test/${i?.image}`,
          })
        })

      // Force re-render carousel
      carouselReloadKey.value++
    }

    const imageInfoToTableData = (info: ExampleImageInfo) => {
      return [
        { name: 'BMS', value: info.bms || '-' },
        { name: '僧帽筋厚さ', value: info.trapeziusThickness || '-' },
        { name: '僧帽筋上皮皮下脂肪厚さ', value: info.trapeziusSubcutaneousFatThickness || '-' },
        { name: '皮下脂肪厚さ', value: info.subcutaneousFatThickness || '-' },
        { name: '広背筋厚さ', value: info.latissimusThickness || '-' },
      ]
    }

    return {
      l2gh,
      l2gl,
      l3gh,
      l3gl,
      isRunning,
      predictedBms,
      searchBms,
      searchTrapeziusThickness,
      searchTrapeziusSubcutaneousFatThickness,
      searchSubcutaneousFatThickness,
      searchLatissimusThickness,
      similarImages,
      carouselReloadKey,
      predict,
      search,
      imageInfoToTableData,
    }
  },
})
